import React from 'react';
import { Popover } from '../popover/Popover';
import { TooltipWrapper, TooltipTitle, TooltipDescription } from './styled';

type ITooltipProps = {
  /**
   * 气泡中的主标题文本
   */
  title?: string | React.ReactNode;
  /**
   * 气泡中的描述性文案
   */
  description?: string | React.ReactNode;
  /**
   * 设置气泡提示出现的位置
   */
  placement?: 'top' | 'bottom' | 'right' | 'left';
  /**
   * 子节点
   */
  children: React.ReactElement;
  /**
   * 挂在容器
   */
  getContainer?: () => HTMLElement;
}

export const tooltipOverlayRestStyle: React.CSSProperties = { border: 'unset', boxShadow: 'unset', backgroundColor: 'transparent' }

export const TooltipContent = React.memo((
  { title, description }: { title?: string | React.ReactNode, description?: string | React.ReactNode }
) => {
  return (
    <TooltipWrapper>
      {title && <TooltipTitle>{title}</TooltipTitle>}
      {description && <TooltipDescription>{description}</TooltipDescription>}
    </TooltipWrapper>
  );
});

export const Tooltip: React.FC<ITooltipProps> = React.memo((props) => {
  const {
    children,
    title,
    description,
    placement,
    getContainer,
  } = props;

  return (
    <Popover
      content={
        <TooltipContent title={title} description={description} />
      }
      style={tooltipOverlayRestStyle}
      placement={placement}
      placementOffset={[4, 0]}
      trigger="hover"
      getContainer={getContainer}
      animation
    >
      {children}
    </Popover>
  );
});